import React from 'react';
import { Permission } from '../../../types/role';

interface PermissionMatrixProps {
  permissions: Permission[];
  setPermissions: (perms: Permission[]) => void;
}

/**
 * UI for assigning permissions to a role.
 */
const allPermissions: Permission[] = [
  { id: 'api_read_access', name: 'API Read Access' },
  { id: 'api_write_access', name: 'API Write Access' },
  { id: 'cluster_management', name: 'Cluster Management' },
  { id: 'deployment_management', name: 'Deployment Management' },
  { id: 'monitoring_management', name: 'Monitoring Management' },
  { id: 'log_access', name: 'Log Access' },
  { id: 'security_scanning', name: 'Security Scanning' },
  // Add more as needed
];

const PermissionMatrix: React.FC<PermissionMatrixProps> = ({ permissions, setPermissions }) => {
  const togglePermission = (perm: Permission) => {
    if (permissions.some(p => p.id === perm.id)) {
      setPermissions(permissions.filter(p => p.id !== perm.id));
    } else {
      setPermissions([...permissions, perm]);
    }
  };

  return (
    <div className="grid grid-cols-2 gap-2">
      {allPermissions.map(perm => (
        <label key={perm.id} className="flex items-center gap-2">
          <input
            type="checkbox"
            checked={permissions.some(p => p.id === perm.id)}
            onChange={() => togglePermission(perm)}
          />
          {perm.name}
        </label>
      ))}
    </div>
  );
};

export default PermissionMatrix;
